.consultation01Wrapper {
  display: flex;
  flex-direction: column;
  flex: 1;
  gap: 40px;
  align-items: center;
  padding: 40px;
  background-color: rgb(26, 35, 53);
}

.text {
  font-size: 20px;
  color: black;
}

.card {
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  gap: 10px;
  width: 600px;
  min-height: 400px;
  padding: 30px;
  border: 1px solid black;
  border-radius: 10px;
  background-color: white;
}

.jokesContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 10px;
}



